// @flow
import React, { useState } from 'react';
import type { Element } from 'react';
import { AppointmentForm as SchedulerAppointmentForm } from '@devexpress/dx-react-scheduler-material-ui';
import BasicLayout from './BasicLayout';
import { Layout } from './Layout';
import { CommandLayout } from './CommandLayout';
import { connectProps } from '@devexpress/dx-react-core';

export const AppointmentForm = (): Element<typeof SchedulerAppointmentForm> => {
  const [isSaving, setIsSaving] = useState(false);
  const BasicLayoutComponent = connectProps(BasicLayout, () => ({
    onSave: () => setIsSaving(true),
  }));

  const CommandLayoutComponent = connectProps(CommandLayout, () => ({
    isSaving,
    setIsSaving,
  }));

  return (
    <SchedulerAppointmentForm
      layoutComponent={Layout}
      basicLayoutComponent={BasicLayoutComponent}
      commandLayoutComponent={CommandLayoutComponent}
    />
  );
};
