// @flow
import React from 'react';
import type { Element } from 'react';
import { Box, FormHelperText, makeStyles } from '@material-ui/core';
import type { TQuestion } from '../../../../types';
import { Checkbox } from '../../../Elements';
import { useFormikContext } from 'formik';

const useStyles = makeStyles(theme => ({
  after: {
    ...theme.typography.subtitle,
  },
}));

type Props = {
  question: TQuestion,
};

export const CheckboxField = ({ question }: Props): Element<typeof Box> => {
  const classes = useStyles();
  const { after, before, required, code } = question;
  const { values, handleChange } = useFormikContext();

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', my: 1 }}>
      <Checkbox
        checked={values[code]}
        onChange={handleChange}
        name={code}
        color="primary"
        label={before}
        required={required}
      />
      {Boolean(after) && (
        <FormHelperText className={classes.after}>{after}</FormHelperText>
      )}
    </Box>
  );
};
