import Request from './Request';
import { PATHS } from './constants';

class ProfilesService extends Request {
  getProfileById({ id, fields }) {
    if (!id) return {};

    return this.axios({
      method: 'GET',
      url: `${PATHS.PROFILES}/${id}`,
      params: {
        fields,
      },
    });
  }
}

export default new ProfilesService();
