// @flow
import React from 'react';
import type { Element } from 'react';
import MomentUtils from '@date-io/moment';
import { useFormikContext } from 'formik';
import {
  Collapse,
  FormControlLabel,
  Switch,
  Grid,
  RadioGroup,
  Radio,
} from '@material-ui/core';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { DATE_PICKER_FORMAT } from '../helpers';
import moment from 'moment';

export const RepeatSection = (): Element<'div'> => {
  const { values, handleChange, setFieldValue } = useFormikContext();

  const handleDateChange = (field: string) => (date: Object) => {
    setFieldValue(field, moment(date).format(DATE_PICKER_FORMAT));
  };

  return (
    <div>
      <FormControlLabel
        control={
          <Switch
            name="repeat"
            checked={values.repeat}
            onChange={handleChange}
            color="primary"
          />
        }
        label="Repeat"
      />

      <Collapse in={values.repeat}>
        <div>
          <RadioGroup
            name="repeatType"
            value={values.repeatType}
            onChange={handleChange}
          >
            <FormControlLabel
              value="daily"
              control={<Radio color="primary" />}
              label="Daily"
            />
            <FormControlLabel
              value="weekly"
              control={<Radio color="primary" />}
              label="Weekly"
            />
          </RadioGroup>
        </div>
        <Grid container justifyContent="space-between">
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              disableToolbar
              inputVariant="outlined"
              format={DATE_PICKER_FORMAT}
              margin="normal"
              label="Start date"
              value={new Date(values.startDate)}
              onChange={handleDateChange('startDate')}
            />
            <KeyboardDatePicker
              disableToolbar
              inputVariant="outlined"
              format={DATE_PICKER_FORMAT}
              margin="normal"
              label="End date"
              value={new Date(values.endDate)}
              onChange={handleDateChange('endDate')}
            />
          </MuiPickersUtilsProvider>
        </Grid>
      </Collapse>
    </div>
  );
};
