// @flow
import React from 'react';
import type { AbstractComponent } from 'react';
import { connect } from 'react-redux';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { setEndOfWeek, setStartOfWeek } from './redux/Actions';
import { Plugin, Getter } from '@devexpress/dx-react-core';

const IntegratedDates = ({ setStartOfWeek, setEndOfWeek }) => {
  const startViewDateComputed = ({ startViewDate }) => {
    setStartOfWeek(startViewDate);
    return startViewDate;
  };

  const endViewDateComputed = ({ endViewDate }) => {
    setEndOfWeek(endViewDate);
    return endViewDate;
  };

  return (
    <Plugin name="IntegratedDates">
      <Getter name="startViewDate" computed={startViewDateComputed} />
      <Getter name="endViewDate" computed={endViewDateComputed} />
    </Plugin>
  );
};

type PropsWithoutRedux = {};

const mapDispatchToProps = {
  setStartOfWeek,
  setEndOfWeek,
};

export default (connect(
  undefined,
  mapDispatchToProps,
)(withErrorBoundary(IntegratedDates)): AbstractComponent<PropsWithoutRedux>);
