// @flow
import React, { Fragment } from 'react';
import type { Element, AbstractComponent } from 'react';
import Paper from '@material-ui/core/Paper';
import { ViewState, EditingState } from '@devexpress/dx-react-scheduler';
import {
  Scheduler,
  WeekView,
  Appointments,
  AppointmentTooltip,
  Toolbar,
  DateNavigator,
  TodayButton,
} from '@devexpress/dx-react-scheduler-material-ui';
import _get from 'lodash/get';
import DayScaleCell from './DayScaleCell';
import TimeTableCell from './TimeTableCell';
import Appointment from './Appointment';
import TooltipContent from './TooltipContent';
import { DEFAULT_DURATION } from './helpers';
import { connect } from 'react-redux';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { createStructuredSelector } from 'reselect';
import _isEmpty from 'lodash/isEmpty';
import IntegratedDates from './IntegratedDates';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import { Loader } from 'react-loaders';
import BlockUi from 'react-block-ui';
import useConfig from './hooks/useConfig';
import useAppointments from './hooks/useAppointments';
import useError from './hooks/useError';
import { AppointmentForm } from './AppointmentForm';

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '100px',
    fontFamily: theme.typography.fontFamily.avenirLT,
  },
  loading: {
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.common.white,
  },
}));

type Props = {
  config: Object,
  profileId: string,
  isLoading: boolean,
  appointments: Object[],
};

const Calendar = ({
  config,
  profileId,
  isLoading,
  appointments,
}: Props): Element<'div' | typeof Fragment> => {
  const classes = useStyles();

  useConfig({ profileId });
  useAppointments({ profileId });
  useError();

  const onCommitChanges = () => {};

  if (_isEmpty(config) && !isLoading) return <></>;

  return (
    <BlockUi
      tag="div"
      blocking={isLoading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx(classes.container, {
        [classes.loading]: isLoading,
      })}
    >
      <Paper elevation={3}>
        <Scheduler data={appointments}>
          <ViewState />
          <WeekView
            timeTableCellComponent={TimeTableCell}
            dayScaleCellComponent={DayScaleCell}
            cellDuration={
              _get(config, 'times.slotLengthMinutes') || DEFAULT_DURATION
            }
          />
          <EditingState onCommitChanges={onCommitChanges} />
          <IntegratedDates />
          <Toolbar />
          <DateNavigator />
          <TodayButton />
          <Appointments appointmentComponent={Appointment} />
          <AppointmentTooltip
            showCloseButton
            contentComponent={TooltipContent}
          />
          <AppointmentForm />
        </Scheduler>
      </Paper>
    </BlockUi>
  );
};

type PropsWithoutRedux = {};

const structuredSelector = createStructuredSelector({
  config: state => _get(state, ['calendar', 'config']),
  isLoading: state => _get(state, ['calendar', 'isLoading']),
  appointments: state => _get(state, ['calendar', 'appointments']),
});

export default (connect(
  structuredSelector,
  undefined,
)(withErrorBoundary(Calendar)): AbstractComponent<PropsWithoutRedux>);
