// @flow
import * as React from 'react';
import type { AbstractComponent } from 'react';
import type { Element } from 'react';
import { BookingForm, SellerBookingForm } from '../BookingForm';
import { connect } from 'react-redux';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { createStructuredSelector } from 'reselect';
import _get from 'lodash.get';

export const BasicLayout = (props: Object): Element<typeof BookingForm> => {
  const { appointmentData, property, onSave } = props;
  const FormComponent = property ? SellerBookingForm : BookingForm;

  return <FormComponent appointmentData={appointmentData} onSave={onSave} />;
};

const structuredSelector = createStructuredSelector({
  property: state => _get(state, ['calendar', 'property']),
});

export default (connect(structuredSelector)(
  withErrorBoundary(BasicLayout),
): AbstractComponent<{}>);
