// @flow
import * as React from 'react';
import Paper from '@material-ui/core/Paper';
import _get from 'lodash/get';
import Typography from '@material-ui/core/Typography';
import { formatTime } from './helpers';
import { makeStyles } from '@material-ui/core/styles';
import {
  AccessTime as AccessTimeIcon,
  Event as EventIcon,
} from '@material-ui/icons';

const useStyles = makeStyles(theme => ({
  wrapper: {
    padding: theme.spacing(2),
    paddingTop: 0,
  },
  section: {
    marginBottom: theme.spacing(1),
  },
}));

const TooltipContent = ({ appointmentData }: Object): React.Element<'div'> => {
  const classes = useStyles();
  const { displayDate, displayTime } = formatTime(appointmentData);

  return (
    <Paper className={classes.wrapper}>
      <Typography variant="h3" color="primary" className={classes.section}>
        {_get(appointmentData, 'title', '')}
      </Typography>
      <div className={classes.section}>
        <Typography variant="h5">
          <EventIcon /> {displayDate}
        </Typography>
      </div>
      <div>
        <Typography variant="h5">
          <AccessTimeIcon /> {displayTime}
        </Typography>
      </div>
    </Paper>
  );
};
export default TooltipContent;
