// @flow
import * as React from 'react';
import type { AbstractComponent } from 'react';
import { Appointments } from '@devexpress/dx-react-scheduler-material-ui';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import { formatTime, isBusyAppointment } from './helpers';
import { createStructuredSelector } from 'reselect';
import _get from 'lodash.get';
import { connect } from 'react-redux';
import { Typography } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  appointment: {
    padding: theme.spacing(0.5, 1),
    backgroundColor: theme.palette.primary.main,
    color: theme.palette.common.white,
    '&:hover': {
      backgroundColor: theme.palette.primary.main,
    },
  },
  busy: {
    backgroundColor: theme.palette.common.lightGray,
    color: theme.palette.primary.gray,
    '&:hover': {
      backgroundColor: theme.palette.common.lightGray,
    },
  },
  title: {
    overflow: 'hidden',
    fontWeight: 'bold',
    whiteSpace: 'nowrap',
    textOverflow: 'ellipsis',
  },
}));

const Appointment = ({
  property,
  children,
  ...restProps
}: Object): React.Element<'div'> => {
  const { data } = restProps;
  const classes = useStyles();
  const isBuyer = !property;
  const isBusyBooking = isBusyAppointment(data);
  const { title } = data;
  const { displayTime } = formatTime(data);
  return (
    <Appointments.Appointment
      {...restProps}
      className={clsx(classes.appointment, {
        [classes.busy]: isBusyBooking,
      })}
    >
      {isBuyer && isBusyAppointment(data) ? (
        <div>Busy</div>
      ) : (
        <div>
          <Typography variant="subtitle1" classes={{ root: classes.title }}>
            {title}
          </Typography>
          <Typography variant="subtitle1">{displayTime}</Typography>
        </div>
      )}
    </Appointments.Appointment>
  );
};

type PropsWithoutRedux = {};

const structuredSelector = createStructuredSelector({
  property: state => _get(state, ['calendar', 'property']),
});

export default (connect(structuredSelector)(
  Appointment,
): AbstractComponent<PropsWithoutRedux>);
