// @flow
import React, { useEffect } from 'react';
import type { Element } from 'react';

export const CommandLayout = (props: Object): Element<'div'> => {
  const { onCommitButtonClick, isSaving, setIsSaving } = props;

  useEffect(() => {
    if (isSaving) {
      onCommitButtonClick();
      setIsSaving(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isSaving]);

  return <div></div>;
};
