// @flow
import React from 'react';
import type { Element } from 'react';
import { Box, FormHelperText, makeStyles } from '@material-ui/core';
import type { TQuestion } from '../../../../types';
import { TextField } from '../../../Elements/TextField';
import { useFormikContext } from 'formik';

const useStyles = makeStyles(theme => ({
  after: {
    ...theme.typography.subtitle,
  },
}));

type Props = {
  question: TQuestion,
};

export const SimpleField = ({ question }: Props): Element<typeof Box> => {
  const classes = useStyles();
  const { code, before, after, required } = question;
  const { values, handleChange } = useFormikContext();

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', my: 1 }}>
      <TextField
        name={code}
        variant="outlined"
        placeholder=""
        label={before}
        value={values[code]}
        onChange={handleChange}
        required={required}
      />
      {Boolean(after) && (
        <FormHelperText className={classes.after}>{after}</FormHelperText>
      )}
    </Box>
  );
};
