// @flow
import React from 'react';
import type { Element } from 'react';
import { Box, makeStyles, Typography } from '@material-ui/core';
import QueryBuilderOutlinedIcon from '@material-ui/icons/QueryBuilderOutlined';
import { getAppointmentTypeDuration } from '../../helpers';
import { ToggleButton, ToggleButtonGroup } from '@material-ui/lab';
import { useFormikContext } from 'formik';
import _get from 'lodash.get';

const useStyles = makeStyles(theme => ({
  group: {
    width: '100%',
    margin: theme.spacing(1, 0, 2, 0),
  },
  button: {
    padding: theme.spacing(1.5, 2),
    textTransform: 'none',
  },
  buttonLabel: {
    width: '100%',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  duration: {
    display: 'flex',
    alignItems: 'center',
  },
  selected: {
    '&&, &&:hover': {
      backgroundColor: theme.palette.primary.main,
    },
  },
}));

type Props = {
  appointmentTypes: Object[],
};

export const AppointmentTypes = ({
  appointmentTypes,
}: Props): Element<typeof React.Fragment> => {
  const classes = useStyles();
  const { values, setFieldValue } = useFormikContext();
  const value = _get(values, 'appointmentType.id') || '';

  const handleChange = (event, newTypeId) => {
    const newType = appointmentTypes.find(type => type.id === newTypeId);
    setFieldValue('appointmentType', newType);
  };

  return (
    <>
      <Typography variant="h4">Choose an appointment type</Typography>
      <ToggleButtonGroup
        orientation="vertical"
        value={value}
        exclusive
        onChange={handleChange}
        classes={{ root: classes.group }}
      >
        {appointmentTypes.map(type => (
          <ToggleButton
            value={type.id}
            classes={{
              root: classes.button,
              label: classes.buttonLabel,
              selected: classes.selected,
            }}
            key={type.id}
          >
            <Typography variant="h5" color="textPrimary">
              {type.name}
            </Typography>
            <Typography
              variant="subtitle1"
              classes={{ root: classes.duration }}
            >
              <QueryBuilderOutlinedIcon fontSize="small" />
              <Box sx={{ ml: 0.5, mt: 0.25 }}>
                {getAppointmentTypeDuration(type)}
              </Box>
            </Typography>
          </ToggleButton>
        ))}
      </ToggleButtonGroup>
    </>
  );
};
