// @flow
import _get from 'lodash/get';
import moment from 'moment';
import 'moment-timezone';
import { BUSY_APPOINTMENT_NAME } from '../../shared/Consts';

export const DEFAULT_DURATION = 60;
export const CALENDAR_CONFIG_FIELDS =
  'times,times.*,questions,questions.*,services, services.*,services.items.*,actions,actions.*,title,timeZone';
export const ERROR_MESSAGES = {
  getTimeConfig: 'Something went wrong',
};

const DATE_FORMAT = 'YYYY-MM-DD';
const MOMENT_FORMAT = `${DATE_FORMAT} HH:mm:ss`;
const DISPLAY_DATE_FORMAT = 'dddd, MMMM DD, YYYY';
const DISPLAY_TIME_FORMAT = 'hh:mm A';

const mapDateToName = dateString =>
  new Date(dateString)
    .toLocaleString('en-us', { weekday: 'short' })
    .toLowerCase();

const getTimeConfig = ({ config, dateString, key }) => {
  const fieldConfig = _get(config, key) || '';
  const timeZone = _get(config, 'timeZone');
  return convertTimezoneToLocal(`${dateString} ${fieldConfig}`, timeZone);
};

const isActivatedMoment = ({ dateName, config }) =>
  _get(config, `${dateName}Active`) || false;

const isAvailableMoment = ({
  config,
  currentMoment,
  currentDate: dateString,
  dateName,
}) => {
  const startDate = getTimeConfig({
    config,
    dateString,
    key: `${dateName}Start`,
  });
  const endDate = getTimeConfig({ config, dateString, key: `${dateName}End` });

  return (
    currentMoment.isSameOrAfter(startDate) && currentMoment.isBefore(endDate)
  );
};

const isMomentInBreaking = ({
  config,
  currentMoment,
  currentDate: dateString,
}) => {
  const breakStart = getTimeConfig({ config, dateString, key: 'breakStart' });
  const breakEnd = getTimeConfig({ config, dateString, key: 'breakEnd' });

  return (
    currentMoment.isSameOrAfter(breakStart) && currentMoment.isBefore(breakEnd)
  );
};

const isMomentInPast = currentMoment =>
  moment().diff(currentMoment, 'hours') > 0;

type isCellActiveParams = {
  dateString: string,
  config: Object,
};
export const isCellActive = ({
  dateString,
  config,
}: isCellActiveParams): boolean => {
  const dateName = mapDateToName(dateString);
  const currentMoment = moment(dateString);

  if (isMomentInPast(currentMoment)) return false;
  if (!isActivatedMoment({ dateName, config })) return false;

  const currentDate = currentMoment.format(DATE_FORMAT);

  const isAvailable = isAvailableMoment({
    config,
    currentMoment,
    currentDate,
    dateName,
  });
  if (!isAvailable) return false;

  const isBreakingMoment = isMomentInBreaking({
    config,
    currentMoment,
    currentDate,
  });
  return !isBreakingMoment;
};

export const formatTime = ({ startDate, endDate }: Object): Object => {
  const displayDate = moment(startDate).format(DISPLAY_DATE_FORMAT);
  const displayTime = `${moment(startDate).format(
    DISPLAY_TIME_FORMAT,
  )} - ${moment(endDate).format(DISPLAY_TIME_FORMAT)}`;
  return { displayDate, displayTime };
};

export const getAppointmentTypeDuration = ({
  numberOfSlots,
  slotLengthMinutes,
}: Object): string => {
  return `${numberOfSlots * slotLengthMinutes} minutes`;
};

export const isBusyAppointment = (data: Object): boolean => {
  const { services } = data;
  const busyAppointmentType = services.find(
    service => service.name.toLowerCase() === BUSY_APPOINTMENT_NAME,
  );
  return Boolean(busyAppointmentType);
};

export const convertTimezoneToLocal = (
  dateString: string,
  timeZone: string,
): string =>
  timeZone
    ? moment.tz(dateString, timeZone).local().format(MOMENT_FORMAT)
    : moment(dateString, MOMENT_FORMAT);
