import Request from './Request';
import { BOOKING_FIELDS, PATHS } from './constants';
import _get from 'lodash/get';
import moment from 'moment';
import parse from 'parse-link-header';
import { remapBookings } from './helpers';

class BookingsService extends Request {
  async createBooking({ profileId, data }) {
    const booking = await this.axios({
      method: 'POST',
      url: `${PATHS.PROFILES}/${profileId}/${PATHS.BOOKINGS}`,
      params: {
        fields: BOOKING_FIELDS,
      },
      data: {
        ...data,
      },
    });
    return remapBookings([booking]);
  }

  async createBookings({ profileId, data }) {
    let cur = 0;
    let errors = [];
    let bookings = [];
    while (cur !== data.length) {
      try {
        const res = await this.axios({
          method: 'POST',
          url: `${PATHS.PROFILES}/${profileId}/${PATHS.BOOKINGS}`,
          params: { fields: BOOKING_FIELDS },
          data: data[cur],
        });
        bookings.push(res);
      } catch (error) {
        errors.push(data[cur]);
      }

      cur = cur + 1;
    }
    return { bookings: remapBookings(bookings), errors };
  }

  async getBookings({ profileId, from, to }) {
    if (!profileId) return [];

    let needMore = true;
    let nextPage = '';
    let bookings = [];

    while (needMore) {
      const res = !nextPage
        ? await this.axios({
            method: 'GET',
            url: `${PATHS.PROFILES}/${profileId}/${PATHS.BOOKINGS}`,
            params: {
              fields: BOOKING_FIELDS,
              jumpToDate: moment(from).format('YYYY-MM-DD'),
            },
            needResHeaders: true,
          })
        : await this.axios({
            method: 'GET',
            url: nextPage,
            needResHeaders: true,
          });

      const resLink = _get(res, ['headers', 'link'], '');
      nextPage = _get(parse(resLink), ['next', 'url'], '');

      const tmpBookings = _get(res, ['data']);
      bookings = [...bookings, ...tmpBookings];
      const lastItemStartDate = _get(
        tmpBookings,
        [tmpBookings.length - 1, 'startsAt'],
        '',
      );

      needMore = nextPage && moment(lastItemStartDate).isBefore(to);
    }

    return remapBookings(bookings);
  }
}

export default new BookingsService();
