// @flow
import type { TQuestion } from '../../../types';
import { Auth } from 'aws-amplify';
import { TYPES } from './constants';
import moment from 'moment';
import 'moment-timezone';
import _get from 'lodash.get';

const BOOKING_FORMAT = 'YYYY-MM-DDTHH:mm:ss';
const BOOKING_DATE_FORMAT = 'YYYY-MM-DD';
const BOOKING_TIME_FORMAT = 'HH:mm:ss';
export const DATE_PICKER_FORMAT = 'MM/DD/YYYY';
const WEEK_DAYS = 7;

const defaultValues = {
  [TYPES.SIMPLE]: '',
  [TYPES.CHECKBOX]: false,
  [TYPES.MULTI_DROPDOWN]: '',
};

const REPEAT_TYPE = {
  daily: 'daily',
  weekly: 'weekly',
};

export const generateInitialValues = (questions: TQuestion[]): Object => {
  return questions.reduce(
    (acc, { code, type }) => ({ ...acc, [code]: defaultValues[type] }),
    {
      appointmentType: undefined,
    },
  );
};

export const generateSellerValues = ({
  property,
  appointmentData,
  config,
}: Object): Object => {
  const { questions } = config;
  const { startDate } = appointmentData;
  const date = moment(startDate).format(DATE_PICKER_FORMAT);
  const address = _get(property, 'PropertyData.FullAddress', '');
  const email = _get(Auth, ['user', 'attributes', 'email'], '');
  return {
    ...generateInitialValues(questions),
    FNAME: address,
    EMAIL: email,
    repeat: false,
    repeatType: REPEAT_TYPE.daily,
    startDate: date,
    endDate: date,
  };
};

const prepareQuestionsAnswers = (questions: TQuestion[], values): Object => {
  return questions.map(({ code }) => ({ code, string: values[code] }));
};

export const prepareBookingData = ({
  appointmentData,
  values,
  config,
}: Object): Object => {
  const { title, questions } = config;
  const { FNAME: name, appointmentType } = values;
  const { startDate } = appointmentData;

  return {
    title: `${name} and ${title}`,
    startsAt: moment(startDate).format(BOOKING_FORMAT),
    tentative: 'NOT_USED',
    timeZone: moment.tz.guess(),
    cancelled: false,
    units: 1,
    services: [{ id: appointmentType.id, name: appointmentType.name }],
    answers: prepareQuestionsAnswers(questions, values),
  };
};

export const BUSY_APPOINTMENT = 'busy';

export const getBusyAppointmentType = (
  appointmentTypes: Object[],
): Object | typeof undefined => {
  return appointmentTypes.find(({ name }) => name === BUSY_APPOINTMENT);
};

export const prepareSellerBookings = ({
  appointmentData,
  values,
  config,
}: Object): Object[] => {
  const { questions } = config;
  const { startDate: appointmentStartDate } = appointmentData;
  const { repeat, repeatType, startDate, endDate } = values;
  const appointmentTypes = _get(config, 'services.items', []);
  const busyAppointmentType = getBusyAppointmentType(appointmentTypes);
  const busyTitle = 'busy';

  if (!repeat)
    return [
      {
        title: busyTitle,
        startsAt: moment(appointmentStartDate).format(BOOKING_FORMAT),
        tentative: 'NOT_USED',
        timeZone: moment.tz.guess(),
        cancelled: false,
        units: 1,
        services: [busyAppointmentType],
        answers: prepareQuestionsAnswers(questions, values),
      },
    ];

  const startTime = moment(appointmentStartDate).format(BOOKING_TIME_FORMAT);
  const mStartDate = moment(startDate);
  const mEndDate = moment(endDate);
  const diff = mEndDate.diff(mStartDate, 'day');
  const module = repeatType === REPEAT_TYPE.weekly ? WEEK_DAYS : 1;

  const days = Array.from(
    Array(Math.floor(diff / module) + 1),
    (_, i) => i * module,
  );

  return days.map(day => {
    const mDay = moment(startDate).add(day, 'day');
    return {
      title: busyTitle,
      startsAt: `${mDay.format(BOOKING_DATE_FORMAT)}T${startTime}`,
      tentative: 'NOT_USED',
      timeZone: moment.tz.guess(),
      cancelled: false,
      units: 1,
      services: [busyAppointmentType],
      answers: prepareQuestionsAnswers(questions, values),
    };
  });
};

export const getErrorMessages = (bookings: Object[]): Object => {
  const bookingDates = bookings.map(booking => booking.startsAt).join(', ');
  return { message: `Cannot book at dates: ${bookingDates}` };
};
