// @flow
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { BookingsService } from '../../../services/YouCanBook';
import _get from 'lodash/get';
import type { CustomHookProps } from './types';
import { setError, setIsLoading, setAppointments } from '../redux/Actions';

const useAppointments = ({ profileId }: CustomHookProps) => {
  const dispatch = useDispatch();
  const startOfWeek = useSelector(state =>
    _get(state, ['calendar', 'startOfWeek']),
  );
  const endOfWeek = useSelector(state =>
    _get(state, ['calendar', 'endOfWeek']),
  );

  useEffect(() => {
    (async () => {
      if (!endOfWeek) return;
      try {
        dispatch(setIsLoading(true));
        const appointmentData = await BookingsService.getBookings({
          profileId,
          from: startOfWeek,
          to: endOfWeek,
        });
        dispatch(setAppointments(appointmentData));
      } catch (error) {
        dispatch(setError(error));
      }
      dispatch(setIsLoading(false));
    })();

    return () => {
      BookingsService.cancel();
    };
    // eslint-disable-next-line
  }, [endOfWeek, profileId]);
};

export default useAppointments;
