// @flow
import React from 'react';
import type { Element } from 'react';
import {
  FormControl,
  MenuItem,
  Select as MuiSelect,
  FormLabel,
  makeStyles,
} from '@material-ui/core';
import clsx from 'clsx';

const useStyles = makeStyles(theme => ({
  wrapper: {
    '& .MuiOutlinedInput-notchedOutline>legend>span': {
      display: 'none',
    },
  },
  label: {
    '&&': {
      color: theme.palette.primary.dark,
      ...theme.typography.h4,
    },
  },
  select: {
    ...theme.typography.h5,
    padding: theme.spacing(1, 1.5),
  },
  item: {
    ...theme.typography.h5,
  },
}));

type Props = {
  name?: string,
  value: string,
  onChange: Function,
  label: string,
  variant?: string,
  required?: boolean,
  options: string[],
  classes?: Object,
};

export const Select = (props: Props): Element<typeof FormControl> => {
  const styleClasses = useStyles();
  const {
    name,
    value,
    onChange,
    label,
    variant,
    required,
    options,
    classes = {},
  } = props;

  return (
    <FormControl required={required} classes={{ root: styleClasses.wrapper }}>
      <FormLabel className={styleClasses.label}>{label}</FormLabel>
      <MuiSelect
        name={name}
        classes={{ root: clsx(styleClasses.select, classes.select) }}
        value={value}
        onChange={onChange}
        label=""
        variant={variant}
        MenuProps={{
          anchorOrigin: {
            vertical: 'bottom',
            horizontal: 'left',
          },
          transformOrigin: {
            vertical: 'top',
            horizontal: 'left',
          },
          getContentAnchorEl: null,
        }}
      >
        <MenuItem
          classes={{ root: clsx(styleClasses.item, classes.item) }}
          value=""
        >
          <em>None</em>
        </MenuItem>
        {options.map((option: string) => (
          <MenuItem
            key={option}
            value={option}
            classes={{ root: clsx(styleClasses.item, classes.item) }}
          >
            {option}
          </MenuItem>
        ))}
      </MuiSelect>
    </FormControl>
  );
};
