// @flow
import React from 'react';
import type { Element } from 'react';
import clsx from 'clsx';
import {
  FormControl,
  FormLabel,
  makeStyles,
  TextField as MuiTextField,
} from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  textField: {
    marginTop: theme.spacing(0.5),
  },
  label: {
    color: theme.palette.primary.dark,
    ...theme.typography.h4,
  },
  input: {
    ...theme.typography.h5,
    padding: theme.spacing(1, 1.5),
  },
}));

type Props = {
  name?: string,
  variant?: string,
  placeholder?: string,
  label: string,
  value: string,
  onChange: Function,
  required?: boolean,
  classes?: Object,
};

export const TextField = (props: Props): Element<typeof MuiTextField> => {
  const styleClasses = useStyles();
  const {
    name,
    variant,
    placeholder = '',
    label,
    value,
    required,
    onChange,
    classes = {},
  } = props;

  return (
    <FormControl required={required}>
      <FormLabel color="secondary" className={styleClasses.label}>
        {label}
      </FormLabel>
      <MuiTextField
        name={name}
        variant={variant}
        placeholder={placeholder}
        value={value}
        onChange={onChange}
        classes={{ root: clsx(styleClasses.textField, classes.textField) }}
        InputProps={{
          classes: {
            input: clsx(styleClasses.input, classes.input),
          },
        }}
      />
    </FormControl>
  );
};
