// @flow
import * as React from 'react';
import type { AbstractComponent } from 'react';
import { WeekView } from '@devexpress/dx-react-scheduler-material-ui';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import _get from 'lodash/get';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { isCellActive } from './helpers';

const useStyles = makeStyles(theme => ({
  disableCell: {
    background: `linear-gradient(-45deg,${theme.palette.action.disabledBackground} 10%,transparent 10%,transparent 50%,${theme.palette.action.disabledBackground} 50%,${theme.palette.action.disabledBackground} 60%,transparent 60%,transparent)`,
    backgroundSize: '8px 8px',
    pointerEvents: 'none',
  },
}));

const TimeTableCell = (props: Object): React.Element<'div'> => {
  const classes = useStyles();
  const { startDate: dateString, timeConfig } = props;

  return (
    <WeekView.TimeTableCell
      {...props}
      className={clsx({
        [classes.disableCell]: !isCellActive({
          dateString,
          config: timeConfig,
        }),
      })}
    />
  );
};

const structuredSelector = createStructuredSelector({
  timeConfig: state => ({
    ..._get(state, 'calendar.config.times'),
    timeZone: _get(state, 'calendar.config.timeZone'),
  }),
});

type PropsWithoutRedux = {};
export default (connect(
  structuredSelector,
  undefined,
)(withErrorBoundary(TimeTableCell)): AbstractComponent<PropsWithoutRedux>);
