// @flow
import React from 'react';
import type { Element } from 'react';
import {
  Checkbox as MuiCheckbox,
  FormControlLabel,
  FormControl,
  makeStyles,
} from '@material-ui/core';
import clsx from 'clsx';

const useStyles = makeStyles(theme => ({
  label: {
    ...theme.typography.h5,
  },
  checkbox: {},
}));

type Props = {
  name?: string,
  required?: boolean,
  checked: boolean,
  onChange: Function,
  label: string,
  color?: string,
  classes?: Object,
};

export const Checkbox = (props: Props): Element<typeof FormControl> => {
  const styleClasses = useStyles();
  const {
    name,
    required,
    checked,
    onChange,
    label,
    color = 'primary',
    classes = {},
  } = props;
  return (
    <FormControl required={required}>
      <FormControlLabel
        classes={{ label: clsx(styleClasses.label, classes.label) }}
        control={
          <MuiCheckbox
            classes={{ root: clsx(styleClasses.checkbox, classes.checkbox) }}
            checked={checked}
            onChange={onChange}
            name={name}
            color={color}
          />
        }
        label={label}
      />
    </FormControl>
  );
};
