// @flow
import type { Element, AbstractComponent } from 'react';
import React from 'react';
import { Box, Button, Divider } from '@material-ui/core';
import { Formik, Form } from 'formik';
import { generateInitialValues, prepareBookingData } from './helpers';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import _get from 'lodash/get';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { AppointmentData } from './AppointmentData';
import { AppointmentTypes } from './AppointmentTypes';
import { BookingsService } from '../../../services/YouCanBook';
import { setIsLoading, setError, addAppointments } from '../redux/Actions';
import { Fields } from './Fields';
import { BUSY_APPOINTMENT_NAME } from '../../../shared/Consts';

type Props = {
  profileId: string | typeof undefined,
  config: Object,
  appointmentData: Object,
  setIsLoading: Function,
  setError: Function,
  addAppointments: Function,
  onSave: Function,
  isLoading: boolean,
};

const BookingForm = ({
  profileId,
  config,
  appointmentData,
  setIsLoading,
  setError,
  addAppointments,
  onSave,
  isLoading,
}: Props): Element<typeof Box> => {
  const questions = _get(config, 'questions', []);
  const appointmentTypes = _get(config, 'services.items', []);
  const buyerAppointmentTypes = appointmentTypes.filter(
    appointment => appointment.name !== BUSY_APPOINTMENT_NAME,
  );
  const initialValues = generateInitialValues(questions);

  const onSubmit = async (values: Object) => {
    try {
      setIsLoading(true);
      const data = prepareBookingData({ appointmentData, values, config });
      const newAppointments = await BookingsService.createBooking({
        profileId,
        data,
      });
      addAppointments(newAppointments);
      onSave();
      setIsLoading(false);
    } catch (error) {
      setIsLoading(false);
      setError(error);
    }
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column' }}>
      <Formik initialValues={initialValues} onSubmit={onSubmit}>
        <Form>
          <AppointmentTypes appointmentTypes={buyerAppointmentTypes} />
          <AppointmentData appointmentData={appointmentData} />
          <Divider />
          <Box sx={{ mt: 4 }}>
            <Fields questions={questions} />
          </Box>
          <Button
            type="submit"
            color="primary"
            disableElevation
            fullWidth
            variant="contained"
            disabled={isLoading}
          >
            Confirm Booking
          </Button>
        </Form>
      </Formik>
    </Box>
  );
};

const structuredSelector = createStructuredSelector({
  profileId: state => _get(state, 'calendar.profileId'),
  config: state => _get(state, 'calendar.config'),
  isLoading: state => _get(state, 'calendar.isLoading'),
});

const mapDispatchToProps = {
  setIsLoading,
  setError,
  addAppointments,
};

type PropsWithoutRedux = {
  appointmentData: Object,
};
export default (connect(
  structuredSelector,
  mapDispatchToProps,
)(withErrorBoundary(BookingForm)): AbstractComponent<PropsWithoutRedux>);
