// @flow
import React from 'react';
import type { Element } from 'react';
import { SimpleField } from './SimpleField';
import { CheckboxField } from './CheckboxField';
import { DropdownField } from './DropdownField';
import type { TQuestion } from '../../../../types';
import { TYPES } from '../constants';

const components = {
  [TYPES.SIMPLE]: SimpleField,
  [TYPES.CHECKBOX]: CheckboxField,
  [TYPES.MULTI_DROPDOWN]: DropdownField,
};

type Props = {
  questions: TQuestion[],
};

export const Fields = ({
  questions,
}: Props): Element<typeof React.Fragment> => {
  return (
    <>
      {questions.map(question => {
        const Component = components[question.type];
        return <Component key={question.code} question={question} />;
      })}
    </>
  );
};
