// @flow
import type { Element, AbstractComponent } from 'react';
import React from 'react';
import { Box, Button, Divider, Typography } from '@material-ui/core';
import { Formik, Form } from 'formik';
import {
  generateSellerValues,
  getErrorMessages,
  prepareSellerBookings,
} from './helpers';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { AppointmentData } from './AppointmentData';
import { BookingsService } from '../../../services/YouCanBook';
import { setIsLoading, setError, addAppointments } from '../redux/Actions';
import { RepeatSection } from './RepeatSection';

type Props = {
  profileId: string | typeof undefined,
  config: Object,
  appointmentData: Object,
  setIsLoading: Function,
  setError: Function,
  addAppointments: Function,
  property: Object,
  onSave: Function,
  isLoading: boolean,
};

const SellerBookingForm = ({
  profileId,
  config,
  appointmentData,
  setIsLoading,
  setError,
  addAppointments,
  property,
  onSave,
  isLoading,
}: Props): Element<typeof Box> => {
  const initialValues = generateSellerValues({
    property,
    appointmentData,
    config,
  });

  const onSubmit = async (values: Object) => {
    try {
      setIsLoading(true);
      const data = prepareSellerBookings({ appointmentData, values, config });
      const { bookings, errors } = await BookingsService.createBookings({
        profileId,
        data,
      });
      if (!_isEmpty(errors)) setError(getErrorMessages(errors));
      addAppointments(bookings);
      onSave();
      setIsLoading(false);
    } catch (error) {
      setIsLoading(false);
      setError(error);
    }
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column' }}>
      <Formik initialValues={initialValues} onSubmit={onSubmit}>
        <Form>
          <Box sx={{ mb: 2 }}>
            <Typography variant="h4">Block available slots</Typography>
          </Box>
          <AppointmentData appointmentData={appointmentData} />
          <Divider />
          <Box sx={{ my: 2 }}>
            <RepeatSection />
          </Box>
          <Button
            type="submit"
            color="primary"
            disableElevation
            fullWidth
            variant="contained"
            disabled={isLoading}
          >
            Confirm Booking
          </Button>
        </Form>
      </Formik>
    </Box>
  );
};

const structuredSelector = createStructuredSelector({
  profileId: state => _get(state, 'calendar.profileId'),
  config: state => _get(state, 'calendar.config'),
  property: state => _get(state, 'calendar.property'),
  isLoading: state => _get(state, 'calendar.isLoading'),
});

const mapDispatchToProps = {
  setIsLoading,
  setError,
  addAppointments,
};

type PropsWithoutRedux = {
  appointmentData: Object,
};
export default (connect(
  structuredSelector,
  mapDispatchToProps,
)(withErrorBoundary(SellerBookingForm)): AbstractComponent<PropsWithoutRedux>);
