// @flow
import { useDispatch } from 'react-redux';
import { useEffect } from 'react';
import { ProfilesService } from '../../../services/YouCanBook';
import { CALENDAR_CONFIG_FIELDS } from '../helpers';
import {
  setConfig,
  setError,
  setIsLoading,
  setProfileId,
} from '../redux/Actions';
import type { CustomHookProps } from './types';

const useConfig = ({ profileId }: CustomHookProps) => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(setProfileId(profileId));

    (async () => {
      dispatch(setIsLoading(true));
      try {
        const data = await ProfilesService.getProfileById({
          id: profileId,
          fields: CALENDAR_CONFIG_FIELDS,
        });
        dispatch(setConfig(data || {}));
      } catch (error) {
        dispatch(setError(error));
      }
      dispatch(setIsLoading(false));
    })();

    return () => {
      ProfilesService.cancel();
    };
    // eslint-disable-next-line
  }, [profileId]);
};

export default useConfig;
